<script>
var formatter = new Intl.NumberFormat('en-US', {
  style: 'currency',
  currency: 'USD'
})
import BaseSpinner from "./ui/BaseSpinner.vue";
export default {
  name: 'MainProject',
  components: {
    BaseSpinner,
  },
  data() {
    return {
      wallet: '',
      year: '2021',
      report: 'txlist',
      isLoading: false,
      transactions: [],
      noResults: false,
    }
  },
  computed: {
    apiString() {
      return `/${this.year}/${this.wallet}?action=${this.report}`;
    },
  },
  methods: {
    test() {
      this.wallet = '0xC8a65Fadf0e0dDAf421F28FEAb69Bf6E2E589963'
      this.year = '2021'
      this.report = 'txlist'
    },
    async fetchWalletTxns() {
      this.noResults = false;
      this.isLoading = true;
      try {
        const res = await fetch(`${this.apiString}`);
        this.transactions = await res.json();
        if (this.transactions.length < 1) {
          this.noResults = true;
        }
        this.isLoading = false;
        return;
      } catch (e) {
        console.log(e);
        this.isLoading = false;
        return;
      } 
    },
    valueChange() {
      this.transactions = [];
    },
    getFullDate(date) {
      const array = date.split("T")
      return array[0]
    },
    getGasETH(gasUsed, gasPrice) {
      const number = (gasUsed/1000000000) * (gasPrice/1000000000);
      return number.toFixed(10);
    },
    fromETHtoUSD(eth, spotRate) {
      return formatter.format(eth * spotRate);
    },
    convertGweitoETH(gwei) {
      const number = gwei/1000000000000000000;
      if (number < 0.0000000001) {
        return 0.00
      } else {
        return number.toFixed(10);
      } 
    },
    formatUSD(usd) {
      return formatter.format(usd);
    },
    linkToEtherscanTx(hash) {
      return "https://etherscan.io/tx/" + hash.toString();
    },
    linkToEtherscanAc(acc) {
      return "https://etherscan.io/address/" + acc.toString();
    }
  },
}
</script>


